<template>
  <div>
      <div class="form-group">
        <select v-model="selectedDay" class="form-control">
          <option
            v-for="(day, key) in days"
            v-bind:value="day.value"
            :key="key"
          >
            {{ day.name }}
          </option>
        </select>
      </div>
      <div class="form-group">
        <select v-model="selectedOpeningTime" class="form-control">
          <option
            v-for="(openingTime, key) in openingTimes"
            :key="key"
            v-bind:value="openingTime.value"
          >
            {{ openingTime.name }}
          </option>
        </select>
      </div>

      <div class="form-group">
        <select v-model="selectedClosingTime" class="form-control">
          <option
            v-for="(closingTime, key) in openingTimes"
            :key="key"
            v-bind:value="closingTime.value"
          >
            {{ closingTime.name }}
          </option>
        </select>
      </div>
      <button
        class="btn btn-primary btn-sm"
        @click.prevent="addDay"
      >
        Add Hours
      </button>
      <hr>
      <div class="jumbotron">
        <Days :list="addedDays" @remove="removeDay" />
      </div>
  </div>
</template>

<script>
import { weekDays, openingTimes } from './mock'

console.log(weekDays, openingTimes)

export default {
  props: {
    initialHours: Array
  },
  components: {
    Days: () => import('./Days.vue')
  },
  data: () => ({
    addedDays: [],
    selectedDay: 0,
    selectedOpeningTime: 8,
    selectedClosingTime: 17,
    days: weekDays,
    openingTimes,
    closingTimes: openingTimes
  }),
  computed: {
    filteredOpeningTimes() {
      return this.openingTimes.filter(
        el => el.value < this.selectedClosingTime
      )
    },
    filteredClosingTimes() {
      return this.closingTimes.filter(el =>
        el.value > this.selectedOpeningTime)
    },
    selectedDayName() {
      return this.days[this.selectedDay].name
    },
    selectedOpeningTimeName() {
      return this.openingTimes[this.selectedOpeningTime].name
    },
    selectedClosingTimeName() {
      return this.closingTimes[this.selectedClosingTime].name
    }
  },
  watch: {
    initialHours() {
      this.addedDays = this.initialHours
    }
  },
  methods: {
    removeDay({ id }) {
      const newDays = this.addedDays.filter(day => day.id !== id)
      this.addedDays = newDays
      this.$emit('updateHours', {
        key: 'hours',
        value: this.addedDays
      })
    },
    addDay() {
      this.addedDays.push({
        id: this.selectedDay,
        openingTime: this.selectedOpeningTime,
        closingTime: this.selectedClosingTime,
        dayName: this.selectedDayName,
        openingTimeName: this.selectedOpeningTimeName,
        closingTimeName: this.selectedClosingTimeName
      })
      this.$emit('updateHours', {
        key: 'hours',
        value: this.addedDays
      })
    }
  }
}
</script>



